[class*="btn"] {
  background-color: #252526;
  border: 1px;
  font-size: 1rem;
  color: white; }

[class*="btn"]:focus {
  outline: none; }

[class*="btn"]:hover {
  background: #606060; }

[class*="btn"]:active {
  background: #1e1e1e; }

.btn__rd {
  border-radius: 15px; }

.btn__sm {
  font-size: 0.9rem;
  padding: 0 0.5rem;
  min-width: 2rem;
  min-height: 2rem; }

.btn__md {
  padding: 0 0.7rem;
  min-width: 2.3rem;
  min-height: 2.3rem; }

.btn__lg {
  font-size: 1.1rem;
  padding: 0 1rem;
  min-width: 2.5rem;
  min-height: 2.5rem; }

.btn__full {
  width: 100%;
  height: 100%;
  margin: 0; }
